export type Nutrition = {
    ml?: number
    g?: number
    count?: number
    calories: number
    fat?: number
    carbs?: number
    minus_carbs?: number
    protein?: number
}

export function plus(...nutritions: Nutrition[]): Nutrition {
    return nutritions.reduce((a, b) => ({
        calories: a.calories + b.calories,
        g: (a.g ?? 0) + (b.g ?? 0),
        ml: (a.ml ?? 0) + (b.ml ?? 0),
        fat: (a.fat ?? 0) + (b.fat ?? 0),
        carbs: (a.carbs ?? 0) + (b.carbs ?? 0),
        minus_carbs: (a.minus_carbs ?? 0) + (b.minus_carbs ?? 0),
        protein: (a.protein ?? 0) + (b.protein ?? 0),
    }), {calories: 0,});
}

export function times(nutrition: Nutrition, scalar: number): Nutrition {
    return {
        calories: Math.round(nutrition.calories * scalar),
        g: Math.round((nutrition.g ?? 0) * scalar),
        ml: Math.round((nutrition.ml ?? 0) * scalar),
        fat: Math.round((nutrition.fat ?? 0) * scalar),
        carbs: Math.round((nutrition.carbs ?? 0) * scalar),
        minus_carbs: Math.round((nutrition.minus_carbs ?? 0) * scalar),
        protein: Math.round((nutrition.protein ?? 0) * scalar),
    };
}
